// BMIの計算クラスを定義
class BMI {
  constructor(height, weight) {
    this.height = height;
    this.weight = weight;
    this.bmi = this.calc();
  }
  calc() {
    const heightM = this.height / 100;
    return this.weight / heightM ** 2;
  }
  print() {
    let res = "普通";
    if (this.bmi >= 25) {
      res = "肥満";
    } else {
      res = "適当";
    }
    console.log("BMI=", this.bmi, res);
  }
}

// テスト
const bmi = new BMI(160, 60);
bmi.print();
